import * as React from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useLocation } from '@reach/router';

const duration = 0.3;

const variants = {
  initial: {
    opacity: 0,
    y: -10,
  },
  exit: {
    opacity: 0,
    transition: { duration: 0, delay: 0, when: 'beforeChidlren' },
  },
  enter: {
    y: 0,
    opacity: 1,
    transition: { duration: duration, delay: 0, when: 'beforeChidlren' },
  },
};

export default function FramerWrapper({ children }) {
  const location = useLocation();
  return (
    <AnimatePresence>
      <motion.div key={location.pathname} variants={variants} initial="initial" animate="enter" exit="exit" id="FramerWrapper">
        <>{children}</>
      </motion.div>
    </AnimatePresence>
  );
}
