import * as React from 'react';
import addToMailchimp from 'gatsby-plugin-mailchimp';
import useIntlHelper from '~src/_i18n/helpers/useIntlHelper';
import * as styles from './Subscribe.module.scss';
import Spinner from '~src/_assets/icons/general/loader';

const SubscribeForm = () => {
  const { formatMessage } = useIntlHelper();
  const [successMessage, setSuccessMessage] = React.useState('subscribe.confirm');

  const [formClassname, setFormClassname] = React.useState('');
  const [email, setEmail] = React.useState(null);

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setFormClassname(styles.loading);
    addToMailchimp(email)
      .then(data => {
        //	alert(`${data.result}: ${data.msg}`);

        if (data.result === 'success') {
          setFormClassname(styles.success);
          document.documentElement.classList.add(styles.subscribeSuccess);
        }
        if (data.result === 'error' && data.msg.includes(email)) {
          setSuccessMessage('subscribe.subscribed');
          setFormClassname(styles.success);
          document.documentElement.classList.add(styles.subscribeSuccess);
        }
        if (data.result === 'error' && data.msg.includes('too many')) {
          setSuccessMessage('subscribe.subscribed');
          setFormClassname(styles.success);
          document.documentElement.classList.add(styles.subscribeSuccess);
        } else {
          setFormClassname(styles.error);
          document.documentElement.classList.add(styles.subscribeFailure);
        }
      })
      .catch((error: Error) => {
        console.log(error);
        // Errors in here are client side
        // Mailchimp always returns a 200
      });
  };
  const handleEmailChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setEmail(event.target.value);
  };
  return (
    <form onSubmit={handleSubmit} className={formClassname} data-members-form="subscribe">
      <div className={styles.formGroup}>
        <input
          className={styles.subscribeEmail}
          placeholder={formatMessage('subscribe.placeholder')}
          autoComplete="false"
          onChange={handleEmailChange}
        />
        <button className="button primary" type="submit">
          <span className={styles.buttonContent}>{formatMessage('subscribe.button')}</span>
          <span className={styles.buttonLoader}>
            <Spinner />
          </span>
        </button>
      </div>
      <div className={styles.messageSuccess}>
        <strong>{formatMessage('subscribe.success')}</strong>
        {formatMessage(successMessage)}
      </div>
      <div className={styles.messageError}>{formatMessage('subscribe.error')}</div>
    </form>
  );
};

export default SubscribeForm;
