import * as React from 'react';
import NavigationItems from './Items/Navigation';
import SocialLinks from './Items/SocialLinks';
import ThemeSwitch from './Items/ThemeSwitch';
import Link from '~src/_i18n/components/Link';
import navLogo from '~src/_assets/images/ghost-logo.png';
import useIntlHelper from '~src/_i18n/helpers/useIntlHelper';
import { useStickynav } from './useStickynav';
import { Helmet } from 'react-helmet';
import * as styles from './Navigation.module.scss';

interface SiteNav {
  className?: string;

  postTitle?: string;
}

const site = {
  title: 'Static Title',
  logo: navLogo,
};

const SiteNav: React.FC<SiteNav> = ({ className, postTitle }) => {
  const { formatMessage } = useIntlHelper();
  return (
    <nav className={`${styles.siteNav} ${className}`}>
      <div className={styles.siteNavLeftWrapper}>
        <div className={styles.siteNavLeft}>
          {site.logo ? (
            <Link className={styles.siteNavLogo} to="/">
              {/* <Logo /> */}
              <img src={site.logo} alt={site.title} />
            </Link>
          ) : (
            <Link className={styles.siteNavLogo} to="/">
              {formatMessage('meta.title')}
            </Link>
          )}
          <div className={styles.siteNavContent}>
            <NavigationItems />

            {postTitle && <span className={`${styles.navPostTitle} ${site.logo && `dash`}`}>{postTitle}</span>}
          </div>
        </div>
      </div>
      <div className={styles.siteNavRight}>
        <div className={styles.socialLinks}>
          <ThemeSwitch />
          <SocialLinks />
        </div>
        <>
          <a className={styles.subscribeButton} href="#subscribe" id="subscribe-button">
            {formatMessage('navigation.subscribe')}
          </a>
        </>
      </div>
    </nav>
  );
};

export interface SiteNavWrapper {
  className?: string;
  postTitle?: string;
  isHome?: boolean;
}
export const Navigation: React.FC<SiteNavWrapper> = ({ className, postTitle, isHome }) => {
  return isHome ? (
    <header className={styles.siteHomeHeader}>
      <SiteNav className={className} postTitle={postTitle} />{' '}
    </header>
  ) : (
    <div className={`outer ${styles.siteNavMain}`}>
      <div className="inner">
        <SiteNav className={className} postTitle={postTitle} />
      </div>
    </div>
  );
};

export const StickyNavigation = ({ template, stickyRef, data }) => {
  const activeClass = template => (template == 'home' ? styles.fixedNavActive : styles.navPostTitleActive);
  const currentClass = useStickynav(stickyRef, false, activeClass(template));
  if (template === 'post') {
    return <Navigation className={currentClass} postTitle={data.post.frontmatter.title} />;
  }
  if (template === 'home') {
    return (
      <>
        <Helmet>
          <body className={styles.homeTemplate} />
        </Helmet>
        <Navigation className={currentClass} />
      </>
    );
  } else return <Navigation className={currentClass} />;
};
