import * as React from 'react';
import useIntlHelper from '~src/_i18n/helpers/useIntlHelper';
import * as styles from './Subscribe.module.scss';

const SubscribeNotification = () => {
  const { formatMessage } = useIntlHelper();
  function handleClose() {
    document.documentElement.classList.remove(styles.subscribeSuccess, styles.subscribeFailure);
  }
  return (
    <>
      <div className={`${styles.subscribeNotification} ${styles.subscribeSuccessMessage}`}>
        <a onClick={() => handleClose()} className={styles.subscribeCloseButton}></a>
        {formatMessage('subscribe.notification.success')}
      </div>
      <div className={`${styles.subscribeNotification} ${styles.subscribeFailureMessage}`}>
        <a onClick={() => handleClose()} className={styles.subscribeCloseButton}></a>
        {formatMessage('subscribe.notification.failure')}
      </div>
    </>
  );
};

export default SubscribeNotification;
