import * as React from 'react';
import * as cx from 'classnames';
import * as styles from './Footer.module.scss';
import useIntlHelper from '~src/_i18n/helpers/useIntlHelper';

const Footer = () => {
  const { formatMessage, intl } = useIntlHelper();

  return (
    <footer className={cx(styles.siteFooter, `outer`)}>
      <div className={cx(styles.siteFooterContent, `inner`)}>
        <section className="copyright">
          {formatMessage('meta.title')} {formatMessage('footer.copyright')}
          {intl.formatDate(new Date(2010, 1, 1), { year: 'numeric' })}-{intl.formatDate(new Date(), { year: 'numeric' })}
        </section>
        <nav className={styles.siteFooterNav}>
          <a href="/">{formatMessage('footer.latest')}</a>
          <a href="/">{formatMessage('footer.facebook')}</a>
          <a href="/about">{formatMessage('meta.title')}</a>
        </nav>
      </div>
    </footer>
  );
};

export default Footer;
