//import { Index } from '~src/pages/__generated__/Index';
import { Post } from '~src/templates/__generated__/Post';
import { Page } from '~src/templates/__generated__/Page';
import { Author } from '~src/templates/__generated__/Author';
import { Tag } from '~src/templates/__generated__/Tag';

interface MetaDataInterface {
  title: string;
  desc: string;
  image: string;
  date?: string;
  author?: string;
  twitter?: string;
}
export function getMetadata(
  data: Page | Post | Tag | Author,
  template: string,
  site: {
    title: string;
    desc: string;
    image: string;
    url: string;
    twitter: string;
    language: string;
    locale: string;
    direction: string;
    notFound: string;
  },
  formatMessage: { (id: string): string; (arg0: string): unknown },
): MetaDataInterface {
  if (template === 'home') {
    const seo = {
      title: site.title,
      desc: site.desc,
      image: site.image,
    };
    return seo;
  }
  if (template === 'notfound') {
    const seo = {
      title: site.notFound,
      desc: site.desc,
      image: site.image,
    };
    return seo;
  }
  if (template === 'page') {
    const seo = {
      title: (data as Page).page.frontmatter.title,
      desc: (data as Page).page.excerpt,
      image: site.image,
    };
    return seo;
  }
  if (template === 'post') {
    const seo = {
      title: (data as Post).post.frontmatter.title,
      desc: (data as Post).post.excerpt,
      image: `${site.url}${(data as Post).post.frontmatter.image.childImageSharp.gatsbyImageData.images.fallback.src}`,
      date: (data as Post).post.frontmatter.date,
      author: formatMessage(`authors.${(data as Post)?.post?.frontmatter?.authors[0]?.yamlId}.title`) || site.title,
    };

    return seo;
  }

  if (template === 'tag') {
    const seo = {
      title: formatMessage(`tags.${(data as Tag).tag.yamlId}.title`),
      desc: formatMessage(`tags.${(data as Tag).tag.yamlId}.desc`),
      image: `${site.url}${(data as Tag).tag.image.childImageSharp.gatsbyImageData.images.fallback.src}`,
    };
    return seo;
  }
  if (template === 'author') {
    const seo = {
      title: formatMessage(`authors.${(data as Author).author.yamlId}.title`),
      desc: formatMessage(`authors.${(data as Author).author.yamlId}.bio`),
      image: `${site.url}${(data as Author).author.avatar.childImageSharp.gatsbyImageData.images.fallback.src}`,
      twitter: (data as Author).author.twitter,
    };
    return seo;
  }
}
